            id: this.nodes.length, //  id\n                type: type, // \n                data: data, // \n            }));\n        },\n    },\n    data: function() {\n        return {\n            selectingNodeIndex: -1,\n        }\n    }\n}\n</script>\n\n<style>\n    .area {\n        position: relative,\n    }\n</style>"],"sourceRoot":""}]);
