// 

import { Config } from 'models/Config'

const FileName = 'assets/config.json';

export default {
    // 
    loadConfig: function(callback) {
        fetch(FileName)
            .then(response => {
                return response.json();
            })
            .then(json => {
                console.log(json);
                Config.update(json);
                callback();
            });
    }
};